/* src/components/Initiatives/Initiative.css */

.initiative-page {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  
    /* Fill the screen minus header if your Header is ~80px tall */
    min-height: calc(100vh - 80px);
    margin-top: 80px; /* push content below the header */
  
    padding: 2rem;
    background: #FFFFFF;
    box-sizing: border-box;
  }
  
  @media (max-width: 768px) {
    .initiative-page {
      flex-direction: column;
      margin-top: 80px;
    }
  }
  
  .initiative-content {
    max-width: 600px;
    margin-right: 2rem;
  }
  
  .initiative-title {
    font-size: 3rem;
    font-weight: bold;
    margin-bottom: 1rem;
  }
  
  .initiative-time {
    font-size: 1.5rem;
    color: #555;
    margin-bottom: 1rem;
  }
  
  .initiative-description {
    font-size: 1.2rem;
    line-height: 1.8;
    margin-bottom: 1.5rem;
  }
  
  .initiative-links {
    margin-top: 1rem;
  }
  
  .initiative-link {
    display: inline-block;
    margin-right: 1rem;
    padding: 0.5rem 1rem;
    background-color: #004a7f;
    color: #fff;
    text-decoration: none;
    border-radius: 4px;
  }
  .initiative-link:hover {
    background-color: #003b66;
  }
  
  .initiative-director,
  .initiative-team {
    margin-top: 1rem;
  }
  
  .initiative-image img {
    max-width: 300px;
    max-height: 300px;
    object-fit: cover;
    border-radius: 8px;
  }